import Enemy from "../basic/enemy";

export default class Player extends Enemy
{   
    render() {
        let html = super.render();
        const new_html = html.replace('btn-warning', 'btn-success');

        return new_html;
    };
    renderActions() {
        let returnHtmlActions = 'niezdefiniowano';
        switch (this.type) {
            case 'kreatura':
                returnHtmlActions = `
                    <ul>
                        <li>zasig widzenia: 36m 120ft 24[ ];</li>
                        <li>intuicja pasywna: 13 (18 z runy);</li>
                        <li>pasywna percepcja: ${this.pp}</li>
                        <li>Zwikszenie rozmiaru (duy rozmiar) trwa minut dwa razy na dugi odpoczynek,</li>
                    </ul>
                    info:<br>
                    <details>
                        <summary>Runa miecza i kamienia </summary> 
                        <p>
                            podwjna biego czyli ekspertyza w narzdziach, 
                            ktre zna oraz widzenie w czemnoci na 120ft 
                            i stae uatwienie w rzutach na intuicj;
                        </p>
                    </details>
                    <details>
                        <summary>*jeli duy rozmiar:</summary> 
                        <p>
                            <ul>
                            <li>przewaga w testach na si i rzutach obronnych na si</li> 
                            <li>+1k6 do obrae raz na tur;</li> 
                        </p>
                    </details>                 
                `;
                break;

            case 'hum':
                returnHtmlActions = `
                <details>
                    <summary>ma cztery koci k6</summary>
                    <p>
                        jeli nie uda mu si test w jakiej umiejtnoci w ktrej ma biego
                        moe dorzuci wynik z koci k6
                        jeli bdzie sukces to ko si marnuje, jeli nie to ko si nie marnuje.
                    </p>
                </details>
                <details>
                    <summary>magiczne ostrza (18m zasigu) pojawiajce si w rku</summary>
                    <p>
                        1k6 obrae psyhicznych obrae + bonusowe obraenia
                        nie zostawiaj ladw ataku
                        ostrze znika zaraz po rzucie bez znaczenia czy trafi czy nie trafi
                        <br><br>
                        jeli atakujesz takim sztyletem w akcji dodatkowej,
                        wtedy obrae jest 1k4 + bonusowe obraenia
                        <br><br>
                        Hum raz na krtki odpoczynek moe odzyska jedna ko
                        <minus> nie mona zrobi ataku okazyjnego bo nie ma broni w rce
                    </p>
                </details>
                <details>
                    <summary>telepatia</summary>
                    <p>
                        telepatia z dwoma osobami w druynie i mog odpowiada (1 mila odlegoci)
                        Hum musi widzie cel aby nawiza ten kontakt
                        Raz mona tego uy na dugi odpoczynek chyba, e odrzuci kostki,
                        ktre ma do obrae
                    </p>
                </details>
                <details>
                    <summary>Wie gdzie szuka informacji</summary>
                    <p>
                        Kiedy Hum chce si dowiedzie jakiej informacji, czsto wie skd j uzyska.
                    </p>
                </details>
                `;
                break;

            case 'omalen':
                returnHtmlActions = `
                Rozpoznawanie zi jest automatyczne <br>
                Moe rozpoznawa roliny ale nie koniecznie ich waciwoci <br>
                <details>
                    <summary>Pijany mistrz:</summary>
                    <p>
                        <ul>
                            <li>Ma wicej ki </li>
                            <li>+10 ft ruchu po skorzystaniu z dodatkowego ataku pici oraz 
                                nie moe dosta z ataku okazyjnego (wyjtek sentinel - atut)</li>
                            <li>+ruchu czyli nie 45 a 55ft ruchu</li>
                            <li>+latajce w Omalena przedmioty mog mie odjte obraenia, 
                                a jeli obrae nie dostanie to moe odrzuci ten pocisk jeli,
                                mieci mu si w rce (test ataku) (1k6 obrae)
                            </li>
                        </ul>
                    </p>
                </details>
                `;
                break;

            case 'alsariph':
                returnHtmlActions = `
                nie krwawi; ma czarny tatua na ciele wychodzcy od serca na wszystkie strony
                wida go na szyi.
                `;
                break;
        
            default:
                break;
        }

        return returnHtmlActions;
    };
}
