import Enemy from "../basic/enemy";

export default class HobgoblinCaptain extends Enemy {
    // "actions": ["javelin","greatsword","leadership"],
    attackJavelin() {
        const testResult = this.getTestResult(4);
        let html = testResult.html;
        html += this.generateDmg(this.k(6), 2, 'Obraenia kute');
        html += '30/120ft'

        return html;
    };
    attackGreatsword() {
        const testResult = this.getTestResult(4);
        let html = testResult.html;
        html += this.generateDmg(this.roll(6,2), 2, 'Obraenia cite');
        // html += '<br/>zasig 150/600 ft (30[ ]/120[ ])';

        return html;
    };
    attackLeadership() {
        return `Moe wyda specjaln komend, dla swojego sojusznika, ktrych widzi i ma w zasigu 30ft<br>
        Tan sojusznik, jeli rozumie przywdc, moe doda 1k4 do rzutu obronnego lub ataku<br>
        `;
    }
}
