import React, { useEffect, useState } from 'react';
import BackToHome from './BackToHome';
import ConstHelper from './helpers/constHelper';

function Rules(props) {
    const [myStyle, setMyStyle] = useState({width: ConstHelper.getWidthApp()});
    const [content, setContent] = useState('Streszczenie zasad');
    const [rule1Class, set1Class] = useState('d-none');
    const [rule2Class, set2Class] = useState('d-none');
    const [rule3Class, set3Class] = useState('d-none');
    const [rule4Class, set4Class] = useState('d-none');
    const [rule5Class, set5Class] = useState('d-none');
    const [rule6Class, set6Class] = useState('d-none');
    const [rule7Class, set7Class] = useState('d-none');
    const [rule8Class, set8Class] = useState('d-none');

    const changeRule = (title) => {
        setContent(title);
        set1Class('d-none');
        set2Class('d-none');
        set3Class('d-none');
        set4Class('d-none');
        set5Class('d-none');
        set6Class('d-none');
        set7Class('d-none');
        set8Class('d-none');
    }

    useEffect(() => {
        if ('Rules' === props.activeStep) {
            setMyStyle({width: ConstHelper.getWidthApp()});
        } else {
            setMyStyle({width: ConstHelper.getWidthApp(), left: ConstHelper.getWidthApp()});
        }
    }, [props.activeStep]);

    return (
        <div className="panel" style={myStyle}>
            <BackToHome changeVisible={props.changeVisible} />
            <div className='btn' style={{backgroundColor: 'var(--secondary-color)'}}>
                <div onClick={() => changeRule('Streszczenie zasad')}>{content}</div>
                <div className={rule1Class}>
                    tutaj jest do zrobienia modu do liczenia odlegoci ...
                </div>
                <div className={rule2Class}>
                    tutaj jest do zrobienia modu obliczania zasigw
                </div>
                <div className={rule3Class}>
                    <details>
                        <summary>Sia</summary>
                        <ul>
                            <li>
                                Czysta sia fizyczna. amanie, cignicie, dwiganie itp.
                            </li>
                            <li>
                                Trzymanie si powierzchni wspinaczki, gdy kto prbuje Ci zepchn.
                            </li>
                            <li>
                                Szczeglnie daleki skok. Pywanie w wirach.
                            </li>
                            <li>
                                Wyrywanie si z wizw czy wywaanie drzwi.
                            </li>
                            <li>
                                Trzymanie si wozu gdy si go trzymasz i cignie Ci on po ziemi
                            </li>
                        </ul>
                    </details>
                    <ul>
                        <li>Atletyka</li>
                    </ul>
                    <details>
                        <summary>Zrczno</summary>
                        <ul>
                            <li>
                                Refleks, zwinno i rwnowaga.
                            </li>
                            <li>
                                Szybkie lub ciche poruszanie si. Utrzymanie rwnowagi na chwiejnym podou.
                            </li>
                            <li>
                                Kradzie przedmiotu lub ukrywanie go u siebie.
                            </li>
                            <li>
                                Prowadzenie pojazdw. Rozbrajanie puapek. Umiejtne zwizanie jeca.
                            </li>
                            <li>
                                Wyswabadzanie si z wizw.
                            </li>
                            <li>
                                Wytwarzanie maego lub bogatego w szczegy przedmiotu.
                            </li>
                        </ul>
                    </details>
                    <ul>
                        <li>Akrobatyka</li>
                        <li>Skradanie si</li>
                        <li>Zwinne donie</li>
                    </ul>
                    <details>
                        <summary>Kondycja</summary>
                        <p>
                            Miara zdrowia, wigoru i si witalnych. Raczej rozpatrzywany paswnie ale zdarzaj si sytuacje
                            kiedy bohater musi przezwyciy swoje ograniczenia ciaa. Wwczas mona wykona test kondycji.
                        </p>
                        <ul>
                            <li>
                                Wstrzymywanie oddechu
                            </li>
                            <li>
                                wielogodzinny marsz lub praca bez odpoczynku
                            </li>
                            <li>
                                funkcjonowanie bez snu lub bez jedzenia i picia
                            </li>
                            <li>
                                wypicie duszkiem caego kufla piwa
                            </li>
                        </ul>
                    </details>
                    <ul>
                        <li>---</li>
                    </ul>
                    <details>
                        <summary>Inteligencja</summary>
                        <ul>
                            <li>
                                Porozumiewanie si z istotami bez uycia sw.
                            </li>
                            <li>
                                Wycena drogocennego przedmiotu
                            </li>
                            <li>
                                Przygotowanie wiarygodnego przebrania stranika miejsckiego
                            </li>
                            <li>
                                Wygra w potyczce intelektualnej 
                            </li>
                            <li>
                                Odwoa si w rozmowe do swojej wiedzy rzemielniczej.
                            </li>
                        </ul>
                    </details>
                    <ul>
                        <li>Historia</li>
                        <li>Przyroda</li>
                        <li>Religia</li>
                        <li>ledztwo</li>
                        <li>Wiedza tajemna</li>
                    </ul>
                    <details>
                        <summary>Mdro</summary>
                        
                        <ul>
                            <li>
                                Spostrzegawczo i wyczucie. Dostrzeganie mowy ciaa i empatia.
                            </li>
                            <li>
                                Diagnozowanie choroby. Stabilizacja chorego.
                            </li>
                            <li>
                                Omijanie trudnego terenu albo znajdujesz lady bandytw czy jakiego potwora.
                            </li>
                            <li>
                                Przewidywanie jaki wybr moe okaza si lepszy 
                            </li>
                            <li>
                                Oceniasz czy np. lece martwe ciao to nieumary
                            </li>
                        </ul>
                        
                    </details>
                    <ul>
                        <li>Intuicja</li>
                        <li>Medycyna</li>
                        <li>Opieka nad zwierztami</li>
                        <li>Percepcja</li>
                        <li>Sztuka przetrwania</li>
                    </ul>
                    <details>
                        <summary>
                            Charyzma
                        </summary>
                        
                            <ul>
                                <li>
                                    Skuteczno interakcji spoecznych. Wpywowo lub wrcz przeciwnie, podporzdkowywanie si innym.
                                </li>
                                <li>
                                    Skuteczno kamstw albo umiejtno rozmw w specyficznej atmosferze.
                                </li>
                                <li>
                                    Zorientowanie si w gwnych tematach rozmw w karczmie
                                </li>
                                <li>
                                    Znalezienie osoby, z ktr warto porozmawia o plotkach czy krcych informacjach w miecie. 
                                </li>
                            </ul>
                        
                    </details>
                    <ul>
                        <li>Perswazja</li>
                        <li>Oszustwo</li>
                        <li>Wystpy</li>
                        <li>Zastraszanie</li>
                    </ul>
                </div>
                <div className={rule4Class}>
                        Trzeba mie si gdzie ukry. W trakcie walki raczej postaci s skupione na celach i nie da si ukry w jej trakcie.
                        eby si podkra rwnie musz by spenione minimalne warunki ukrycia si.
                        <br/>
                        Kiedy si ukrywasz i kto spodziewa si ciebie rozwizuje si to testem spornym:
                        <br/>
                        Skradanie si vs Percepcja.
                        <br/>
                        Jeli podkradajcy si nie jest spodziewanym gociem wystarczy, e on rzuci.
                        Wynik testu jest konfrontowany z pasywn percepcj osb do ktrych si podkrada.
                    </div>
                <div className={rule5Class}>
                        <p>
                            Puapka musi mie okrelony stopie trudnoci wykrycia
                            oraz stopie trudnoci rozbrojenia. Czsto obie te rzeczy s podbnie trudne.
                            <br/>
                            Niektre dziaania graczy mog z zasady odkrywa puapki i nie potrzeba wwczas kaza im rzuac testu.
                            <br/>
                        </p>
                        Przykadowe puapki:
                        <ul>
                            <li>
                                mechaniczne:
                                <ol>
                                    <li>
                                        <details>
                                            <summary>Prosty d</summary>
                                            <p>
                                                Dzira w ziemi min 3m obraenia zalene od gbkoci.
                                                ST dostrzerzenia: 10
                                            </p>
                                        </details>
                                    </li>
                                    <li>
                                        <details>
                                            <summary>Spadajca sie</summary>
                                            <p>
                                                Pulapka wykorzystuje napit linke, ktora po szarpnieciu
                                                uwalnia sic zawieszona pod sufitem.
                                                Linka jest rozpieta 7,5 centymetra nad ziemia miedzy
                                                dwoma kolumnami albo drzewami. Sie jest zakryta
                                                pajeczynami albo listowiem. ST dostrzezenia linki i sieci
                                                to 10. Udany test Zrecznosci o ST 15 z uzyciem narzedzi
                                                zlodziejskich unieszkodliwia pulapke. Postac bez narzedzi
                                                zlodziejskich moze posluzy sie dowolnym ostrzem narze-
                                                dzia lub broni, ale wykonuje test z utrudnieniem. Nieudany
                                                test uruchamia pulapke.
                                                Wyzwolenie pulapki powoduje, ze sie spada i pokrywa
                                                obszar kwadratu o boku 3 metrw. Stworzenia na tym
                                                obszarze zostaja uwiezione pod siecia i unieruchomione,
                                                musza takze wykonac rzut obronny na Site o ST 10.
                                                W przypadku niepowodzenia zostaja powalone. Stworzenie
                                                moze w ramach swojej akcji wykonac test Sily o SI 10.
                                                W przypadku sukcesu uwalnia siebie abo inna istote. Siec
                                                ma KP 10 i 20 PW. Jesli siec otrzyma 5 obrazer cietych, to
                                                jedna kwadratowa cz o boku 1,5 metra zostaje zniszczona,
                                                a uwiezione pod nia stworzenia uwolnione.
                                            </p>
                                        </details>
                                        
                                    </li>
                                    <li>
                                        koszce ostrza
                                    </li>
                                    <li>
                                        spadajca sie
                                    </li>
                                    <li>
                                        zatrute strzaki
                                    </li>
                                </ol>
                            </li>
                            <li>
                                megiczne:
                                <ol>
                                    <li>
                                        duszce pncza
                                    </li>
                                </ol>
                            </li>
                        </ul>
                        <br/>
                        Tabele rzutu obronnego:
                        <table className="tg">
                            <thead>
                              <tr>
                                <th className="tg-0lax">Moc puapki</th>
                                <th className="tg-0lax">ST rzutu obronnego</th>
                                <th className="tg-0lax">Premia do ataku</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td className="tg-0lax">Uciliwa</td>
                                <td className="tg-0lax">10-11</td>
                                <td className="tg-0lax">+3 do +5</td>
                              </tr>
                              <tr>
                                <td className="tg-0lax">Niebezpieczna</td>
                                <td className="tg-0lax">12-15</td>
                                <td className="tg-0lax">+6 do +8</td>
                              </tr>
                              <tr>
                                <td className="tg-0lax">Zabjcza</td>
                                <td className="tg-0lax">16-20</td>
                                <td className="tg-0lax">+9 do +12</td>
                              </tr>
                            </tbody>
                        </table>

                        <br/>
                        Tabela obrae wg poziomu i dotkliwoci:
                        <table className="tg">
                            <thead>
                              <tr>
                                <th className="tg-0lax">Poziom postaci</th>
                                <th className="tg-0lax">Uciliwa</th>
                                <th className="tg-0lax">Niebezpieczna</th>
                                <th className="tg-0lax">Zabjcza</th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr>
                                <td className="tg-0lax">1-4</td>
                                <td className="tg-0lax">1k10</td>
                                <td className="tg-0lax">2k10</td>
                                <td className="tg-0lax">4k10</td>
                              </tr>
                              <tr>
                                <td className="tg-0lax">5-10</td>
                                <td className="tg-0lax">2k10</td>
                                <td className="tg-0lax">4k10</td>
                                <td className="tg-0lax">10k10</td>
                              </tr>
                              <tr>
                                <td className="tg-0lax">11-16</td>
                                <td className="tg-0lax">4k10</td>
                                <td className="tg-0lax">10k10</td>
                                <td className="tg-0lax">18k10</td>
                              </tr>
                              <tr>
                                <td className="tg-0lax">17-20</td>
                                <td className="tg-0lax">10k10</td>
                                <td className="tg-0lax">18k10</td>
                                <td className="tg-0lax">24k10</td>
                              </tr>
                            </tbody>
                        </table>
                    </div>
                <div className={rule6Class}>
                    dua lub mniejsza istota trafiona sieci zostaje unieruchomiona.
                    Trafione stworzenie moe wykorzysta akcj na wykonanie testu siy o ST 10
                    by uwolni siebie lub inn istot znajdujc si w jej strefie ataku.
                    Zadanie sieci 5pkt obrae citych (KP10) uwalnia stowrzenie bez robienia mu przywdy
                    koczcstan unieruchomienia i niszczc sie.
                    Mona rzuci sie tylko raz w ramach swojej akcji, akcji dodatkowej lub reakcji przeznaczonej na atak.
                </div>
                <div className={rule7Class}>
                    zaklcie "Blinded" (Zalepienie) jest stanem, w ktrym stworzenie jest pozbawione zdolnoci widzenia.<br/>
                    Osoba, stworzenie lub potwr, ktre jest olepione, ma nastpujce cechy:<br/><br/>
                    <ul>
                        <li>
                            Automatycznie niezdolno do wykonywania rzutw ataku: Posta olepiona ma dezorientacj w walce i rzutw ataku przeciwko przeciwnikom wykonuje z uomnoci, co oznacza, e rzuty ataku maj wwczas wady (rzuca si dwiema kostkami i bierze mniejsz warto). Rwnie rzuty ataku przeciwko postaci olepionej maj przewag, co oznacza, e przeciwnik rzuca dwiema kostkami i bierze wiksz warto.
                        </li>
                        <li>
                            Automatycznie niezdolno do korzystania ze zdolnoci zwizanych ze wzrokiem: Posta olepiona nie moe korzysta ze zdolnoci opartych na wzroku, takich jak czytanie, wykrywanie ukrytych przeciwnikw czy rozpoznawanie otoczenia.
                        </li>
                        <li>
                            Rzuty obronne z woli: W niektrych przypadkach posta olepiona moe mie korzyci w obronie przed zaklciami lub zdolnociami, ktre wymagaj rzutu obronnego na Woli.
                        </li>
                    </ul>
                </div>
                <div className={rule8Class}>
                    Posta otrzymuje 1k6 obrae obuchowych od upadku za kade 3 metry (max 20k6). Ponadto bohater jest powalony.</div>
            </div>
            <div style={{margin: '.5em', display: 'grid', gridTemplateColumns: '50% 50%', gridTemplateRows: 'auto auto'}}>
                <div onClick={() => {changeRule('Kalkulator odlegoci'); set1Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Kalkulator odlegoci
                </div>
                <div onClick={() => {changeRule('Typy broni i zasigi'); set2Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Typy broni i zasigi
                </div>
                <div onClick={() => {changeRule('Testy Atrybutw'); set3Class('activeRuleContent')}}
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Testy Atrybutw
                </div>
                <div onClick={() => {changeRule('Ukrywanie si'); set4Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Ukrywanie si
                </div>
                <div onClick={() => {changeRule('Puapki'); set5Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Puapki
                </div>
                <div onClick={() => {changeRule('Upadek'); set6Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Upadek
                </div>
                <div onClick={() => {changeRule('Zalepienie'); set7Class('activeRuleContent')}}
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Zalepienie
                </div>
                <div onClick={() => {changeRule('Sie'); set8Class('activeRuleContent')}} 
                    className='rules-grid-element' style={{backgroundColor: 'var(--gray-color)'}}>
                    Sie
                </div>
            </div>
        </div>
    );
}

export default Rules;