import Enemy from "../basic/enemy";

export default class RotMonster extends Enemy
{   
    shortInfo() {
        return `[+7/2k8+4(obuch)]x2 
        </br> pochonicie jeli oba ataki si powiody 
        <br/> 60ft [12] lepowidzenia`;
    };
    // "actions": ["multiattack","smash","absorption"],
    attackMultiattack() {
        let html = 'Opis ataku wielokrotnego:';
        html += '<br/> 2x walnicie. Jeli trafi dwa razy redni lub may cel cel jest pochwycony';
        html += '<br/> Cel moe si prbowa wysfobodzi o ST 14';
        html += '<br/> Jeli gnilec ma pochwycon ofiar moe uy przeciw niej pochonicia';

        return html;
    };
    attackSmash() {
        const testResult = this.getTestResult(7);
        let html = testResult.html;
        html += this.generateDmg(this.k(8)*2, 4, 'Obraenia obuchowe');

        return html;
    };
    attackAbsorption() {
        let html = 'Pochonita ustota (maa lub rednia) jest';
        html += 'olepiona, unieruchomiona i nie moe oddycha';
        html += '<br/> na pocztku kadej tury gnilca cel musi wykona test obronny na kondycj';
        html += '<br/> ST 14. Jeli nie zda otrzymuje obraenia 2k8+4 obuchowe';
        html += '<br/> Gnilec moe mie tylko jedn wchonit istot w sobie';
        html += '<br/> Gnilec moe si z ni przemieszcza';

        return html;
    }
}
