import Enemy from "../basic/enemy";

export default class BanditCaptain extends Enemy {
    // "actions": ["scimitar","dagger"],
    attackScimitar() {
        const testResult = this.getTestResult(5);
        let html = testResult.html;
        html += this.generateDmg(this.k(6), 3, 'Obraenia cite');

        return html;
    };
    attackDagger() {
        const testResult = this.getTestResult(5);
        let html = testResult.html;
        html += this.generateDmg(this.k(4), 3, 'Obraenia kute');
        html += '<br>20/60ft'

        return html;
    };
    attackReaction() {
        return `
            Parowanie: <br>
            jeli dziery bro i widzi przeciwnika moe doda 2 do swojej klasy pancerza
        `;
    }
}
