import Enemy from "../basic/enemy";

export default class GnollPackLord extends Enemy {
    attackBite() {
        const testResult = this.getTestResult(5);
        let html = testResult.html;
        html += this.generateDmg(this.k(4), 3, 'Obraenia kute');

        return html;
    };
    attackGlaive() {
        const testResult = this.getTestResult(5);
        let html = testResult.html;
        html += this.generateDmg(this.k(10), 3, 'Obraenia cite');
        html += '<br/>z bliska lub zasig 20/60ft';

        return html;
    };
    attackLongBow() {
        const testResult = this.getTestResult(4);
        let html = testResult.html;
        html += this.generateDmg(this.k(8), 2, 'Obraenia kute');
        html += '<br/>zasig 150/600ft';

        return html;
    }
}
