import Enemy from "../basic/enemy";

export default class DireWolf extends Enemy
{   
    shortInfo() {
        return `+5/2k6+3(k) z powaleniem ST 13
        <br/> uatwienie z sojusznikiem obok`;
    };
    // "actions": ["bit"],
    attackBite() {
        const testResult = this.getTestResult(5);
        let html = testResult.html;
        html += this.generateDmg(this.roll(6, 2), 3, 'Obraenia kute');
        html += ' cel ataku musi wykona test siy o ST 13 albo bdzie powalony.';

        return html;
    }
}
